        >
        <button class="close-button" @click="closeModal">&times;</button>
      </div>
    </div>
  </section>
</template>

<script>
import gramImg from "@/assets/teaching.png";

export default {
  name: "BackgroundSection",
  data() {
    return {
      showModal: false,
      isLoading: true,
      imageSrc: gramImg
    };
  },
  methods: {
    closeModal() {
      this.showModal = false;
    },
    onLoadSuccess() {
      this.isLoading = false;
      console.log("Image loaded successfully!");
    },
    onLoadError(err) {
      console.error("Image failed to load:", err);
    },
  },
  watch: {
    showModal(newVal) {
      if (newVal) {
        this.isLoading = true;
      }
    }
  },
  mounted() {
    console.log("gramImg =>", gramImg); 
  }
};
</script>

